const Noticia = props => {
  const { urlToImage, url, title, description, source } = props.noticia;
  let imagen = "";
  if (urlToImage !== "")
    imagen = <img src={urlToImage} alt={title} className="card-img-top" />;
  else imagen = <p className="text-cente my-5">Imagen no disponible</p>;
  return (
    <div className="col-md-6 col-12 mb-4">
      <div className="card">
        <div className="card-image">{imagen}</div>
      </div>
      <div className="carbody">
        <h4 className="card-title">{title}</h4>
        <p className="card-text">{description}</p>
        <p className="card-text">{source.name}</p>
        <a href={url} target="_blank" className="btn btn-primary btn-block">
          Leer Noticia
        </a>
      </div>
    </div>
  );
};

export default Noticia;
